import React, { Component } from 'react';
import Slider from 'react-slick';

class SlickCarousel extends Component {
  render() {
    var settings = {
    className: 'center',
        infinite: true,
        centerPadding: '60px',
        slidesToShow: 3,
      dots: true,
      swipeToSlide: true,
    }
    return (
      <div className='container'>
        <Slider {...settings}>
        <div><img src='http://placekitten.com/g/400/200' alt="a"/></div>
          <div><img src='http://placekitten.com/g/400/200' alt="a" /></div>
          <div><img src='http://placekitten.com/g/400/200' alt="a" /></div>
          <div><img src='http://placekitten.com/g/400/200' alt="a"/></div>
        </Slider>
      </div>
    );
  }
}

export default SlickCarousel;



